





































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import ItemHeader from '@/components/ItemHeader.vue';
import Verify from 'vue2-verify/src/components/Verify.vue';
import {Toast} from 'vant';
import {login, register} from '@/service/user';
import {setLocal} from '@/assets/ts/utils';
import {getCart} from '@/service/cart';

@Component({
  components: {ItemHeader, Verify}
})
export default class Login extends Vue {
  type = 'login';
  username = '';
  password = '';
  verify = false;

  async onSubmit(values: any) {
    const verifySubmit = () => {
      (this.$refs.loginVerifyRef as any).$refs.instance.checkCode();
    };//
    verifySubmit();  //successFun/errorFun


    if (!this.verify) {
      Toast.fail(''); //vant
      return;
    }

    //
    if (this.type === 'login') {
      try {
        const {data} = await login({
          'loginName': values.username,
          'passwordMd5': Vue.prototype.$md5(values.password)
        });
        setLocal('token', data);//localStorage
        Toast.success('');
        await this.$router.push('/') //
        // window.location.href = '/'; //
        window.location.reload()
      } catch (err) {
        Toast.fail(err.message);
      }
    } else {
      try {
        const {data} = await register({
          'loginName': values.username,
          'password': values.password
        });
        Toast.success('');//
        this.type = 'login';
      } catch (err) {
        Toast.fail(err.message);
      }
    }

  }

  successFun(value: any) {
    // console.log('');
    this.verify = true;
    value.refresh();
  }

  errorFun(value: any) {
    // console.log('');
    this.verify = false;
    value.refresh();
  }

  toggle() {
    this.verify = false;
    if (this.type === 'login') {
      this.type = 'register';
    } else {
      this.type = 'login';
    }
  }
}
