

























import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';
import None from '@/components/None.vue';

@Component({
  components: {None}
})
export default class SearchPageList extends Vue {
  @Prop() keyword!: string;

  goodsDetail(item: any) {
    this.$router.push({path: `goodsdetail/${item.goodsId}`});
  }

}
