














import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import ItemHeader from '@/components/ItemHeader.vue';
import {EditUserInfo, getUserInfo, logout} from '@/service/user';
import {Toast} from 'vant';
import {setLocal} from '@/assets/ts/utils';

@Component({
  components: {ItemHeader}
})
export default class Setting extends Vue {
  nickName = '';
  introduceSign = '';
  password = '';
  loginName = '';

  async mounted() {
    const {data} = await getUserInfo();
    this.nickName = data.nickName;
    this.introduceSign = data.introduceSign;
    this.loginName = data.loginName;
  }

  async save() {
    if (!this.password) {
      Toast.fail('');
      return;
    }
    const params = {
      introduceSign: this.introduceSign,
      nickName: this.nickName,
      passwordMd5: Vue.prototype.$md5(this.password)
    };
    try {
      await EditUserInfo(params);
      Toast.success('');
      setTimeout(() => {
        this.$router.push('/user');
      }, 500);
    } catch (e) {
      return;
    }
  }

  async logout() {
    try {
      await logout();
      setLocal('token', '');
      setLocal('checkIdArr', '');
      setLocal('cartCount', '0');
      Toast.success('');
      await this.$router.push('/')
      window.location.reload()
    } catch (e) {
      return;
    }
  }
}
